body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
  display: grid;
  justify-items: center;
  border-radius: 50px;
  background-color: #BA8748;
  font-family: 'Poiret One', sans-serif;
  margin: 30px;
  width: 200px;
  height: 300px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  -webkit-align-items: center;
          align-items: center;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.card .title {
  color: white;
}

.card .episode {
  position: relative;
    top: 15%;
    color: white;
    text-decoration: none;
}

.card .director {
  color: white;
  position: relative;
  top: 0;
}

.card > img {
  border-radius: 50px;
  width: 100px;
  height: 100px;
}

.card .heart {
  width: 20px;
  height: 20px;
  position: relative;
}

.flex-container {
  padding: 0;
  margin: 0;
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.wrap {
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.card-modal {
  display: grid;
  justify-items: center;
  border-radius: 50px;
  background-color: #BA8748;
  font-family: 'Poiret One', sans-serif;
  margin: 30px;
  width: 700px;
  height: 410px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  -webkit-align-items: center;
          align-items: center;
}

.card-modal:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.card-modal .title {
  color: #BA8748;
}

.card-modal .episode {
  position: relative;
    top: 15%;
}

.card-modal .director {
  color: #BA8748;
  position: relative;
  top: 40%;
}

.card-modal > img {
  width: 100px;
  height: 100px;
}

.card-modal .heart {
  width: 20px;
  height: 20px;
  position: relative;
}


