.navigation {
  display: -webkit-flex;
  display: flex;
  list-style: none;
   -webkit-justify-content: space-around;
           justify-content: space-around;
}

.navItem a {
  margin-right: 18px;
  color: #BA8748;
}

h1 {
  color: #BA8748;
}


.home {
  font-family: 'Poiret One', sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;

}
.welcome {
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  display: -webkit-flex;
  display: flex;
}

.headline {
  text-align: center;
  display:block;
  width: 60%;
  margin: 0 auto;
  padding: 0;
  position: relative;
}


.header {
  text-decoration: none;
  display: inline-block;
  max-width: 60%;
  padding: 1em 30px;
  padding-bottom: 0px;
  text-align: center;
  font-family: 'Poiret One', sans-serif;
  text-transform: uppercase;
  font-size: 35px;
  letter-spacing: 10px;
  line-height: 1.5em;
  color: #BA8748;
  @media screen and (max-width: 650px) {
    font-size: 20px;
    letter-spacing: 2px;
  }
}

